@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

.App {
  padding: 48px 0 48px 0;
}

.MuiBottomNavigation-root {
  position: fixed !important;
  bottom: 0 !important;
  right: 0 !important;
  left: 0 !important;
}

.pageName {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 48px;
  background: rgba(1, 163, 157, 0.71);
  margin: 0;
  padding-left: 16px;
  padding-top: 8px;
  line-height: 48px;

  font-family: Roboto;
  font-weight: 500;
  font-size: 16pt;
  color: white;
}

/* Reset */
* {
  padding: 0;
  margin: 0;
}

body {
  background-color: white;
  margin: 24px;
}

h1 {
  font-family: "Roboto", sans-serif;
  font-size: 16pt;
  text-align: left;
  color: rgba(0, 0, 0, 0.87);
  margin-bottom: 16px;
  margin-top: 64px;
}

h5 {
  font-family: "Roboto", sans-serif;
  font-size: 12pt;
  text-align: left;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 16px;
}

.choiceButton {
  display: block !important;
  color: #004b4f !important;
  border: none !important;
  margin: 8px !important;
  padding: 8px !important;
}

.nextButton {
  background: #004b4f !important;
  border-radius: 200px !important;
  color: white !important;
  position: absolute !important;
  bottom: 16px !important;
  right: 16px !important;
}

.barra-azul {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 48px;
  background: rgba(1, 163, 157, 0.71);
}

.MuiFormControl-root {
  width: 100% !important;
  margin-bottom: 16px !important;
}

.MuiFormControl-root {
  margin: 16px;
}

.protocolCard {
  margin: 16px;
  float: left;
}

.container::after {
  content: "";
  clear: both;
  display: table;
}

.whatsapp-link{
  height: 32px;
  line-height: 32px;
  background-color: #004B4F;
  border-radius: 4px;
  color: white !important;
  text-decoration: none;
  font-size: 8pt;
  text-align: left;
  padding-right: 16px;
  font-weight: bold;
}

.whatsapp-link svg{
  float: left;
  margin-top: 3px;
  margin-left: 12px;
  margin-right: 12px;
  font-weight: bold;
}
h4 {
  font-family: "Roboto", sans-serif;
  font-size: 12pt;
  text-align: left;
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 16px;
  margin-top: 24px;
}
.MuiButton-root {
  margin: 4px !important;
  background-color: white !important;
  border: 1px solid #004b4f !important;
  box-sizing: border-box !important;
  padding: 8px !important;
  border-radius: 0px !important;
  color: #004b4f !important;
}

.MuiButton-contained {
  background-color: #004b4f !important;
  color: white !important;
}

.rounded-button {
  margin-top: 24px !important;
  border-radius: 32px !important;
  padding: 8px 32px 8px 32px !important;
  float: right !important;
  clear: right !important;
  margin-bottom: 72px !important;
}

.CloseButton {
  position: absolute !important;
  top: 0px !important;
  right: 0px !important;
}

.Modal{
  max-width: 375px;
  max-height: 375px;
  width: 95vw;
  height: 95vw;
  background-color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.Container{
  text-align: center;
  padding: 8px;
}

.Container h4{
  text-align: center;
  color: rgba(0, 0, 0, 0.87);
  margin-bottom: 5px;
  font-size: 16pt;
}

.Container h6{
  text-align: center;
  color: rgba(0, 0, 0, 0.6);
  font-size: 10pt;
}

.WarningIcon{
  height: 1% !important;
  width: 30% !important;
  color: #01A39D !important;
  margin-top: 24px;
}

.button-holder{
  text-align: center;
  position: absolute;
  bottom: 8px;
  width: 100%;
}

.epi-options{
  margin-top: 8px;
}

.Container .rounded-button{
  margin: 0 auto !important;
  position: absolute;
  bottom: 16px;
  right: 0; left: 0; margin: auto;
}
p {
  margin-top: 4px;
  margin-bottom: 4px;
}

h1 {
  font-family: "Roboto", sans-serif;
  font-size: 16pt;
  text-align: left;
  color: rgba(0, 0, 0, 0.87);
  margin-bottom: 24px;
  margin-top: 0px !important;
}

